.password-filed {
    display: flex;
    flex-direction: column;
}

.password-filed__input {
    padding: 10px;
    width: 300px;
    margin-bottom: 10px;

    font-size: 24px;
    font-family: "VT323";
    color: #0dda0d;

    background-color: transparent;
    border: 2px solid #fff;
}

.password-filed__input:focus {
    border-color: #0dda0d;
}

.password-filed__btn {
    padding: 10px;

    color: #fff;
    font-size: 16px;
    font-weight: 600;

    border: 2px solid #ffffff;
    background-color: transparent;


    cursor: pointer;
}

.countdown {
    margin-bottom: 10px;
    font-size: 70px;
}