.success {
    position: fixed;
    right: 50%;
    top: 50%;
    transform: translate(50%, -50%);

    padding: 50px 70px;

    background-color: #0dda0d;
}

.success span {
    font-size: 70px;
}

.input-success input {
    color: #ffffff;
    text-transform: uppercase;

    background-color: transparent;
    border: 1px solid #fff;
}

.input-success button {
    color: #0dda0d;
    font-weight: 600;

    background-color: #ffffff;
    border: 1px solid #fff;

    cursor: pointer;
}
